.wrapper {
  display: flex;
  flex-direction: column;
  width: 95%;
  margin: auto;
}
.loader {
  width: 100px;
  height: 100px;
  position: absolute;
  top: calc(50% - 50px);
  left: calc(50% - 50px);
}
.loaderText {
  color: white;
  font-size: 23px;
  text-align: center;
}
@media (min-width: 1200px){
  .wrapper {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    width: 95%;
    position: absolute;
    bottom: 1%;
    left: 2.5%;
  }
}