.wrapper {
  display: flex;
  width: 80%;
  justify-content: space-between;
  margin: auto;
  align-content: center;
  background-color: rgba(255, 255, 255, 0.4);
  border-radius: 20px;
  box-shadow: 0 0 25px 25px rgba(255, 255, 255, 0.4);
}
.main {
  width: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  color: white;
  font-size: 24px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  border-radius: 20px;
  box-shadow: 0 0 10px 10px rgba(0, 0, 0, 0.6);
}
.weather {
  width: 40%;
  text-align: center;
}
.img {
  width: 50px;
  height: 40px;
}
.name {
  font-size: 30px;
}

.temp {
  font-size: 25px;
}
.map {
  display: none;
  width: 55%;
  border-radius: 20px;
  box-shadow: 0 0 10px 10px rgba(0, 0, 0, 0.6);
  background-color: rgba(0, 0, 0, 0.6);
}
.weather_det {
  font-size: 17px;
}
@media (min-width: 768px){
  .wrapper {
    width: 60%;
  }
  .img {
    width: 70px;
    height: 60px;
  }
  .weather_det {
    font-size: 20px;
  }
}
@media (min-width: 960px){
  .wrapper {
    width: 90%;
  }
  .main {
    width: 40%;
  }
  .map{
    display: flex;
  }
}
@media (min-width: 1200px){
  .wrapper {
    width: 80%;
    position: absolute;
    bottom: 6%;
    left: 10%;
  }
  .img {
    width: 100px;
    height: 90px;
  }
 
  
}