.header {
  width: 90%;
  margin: auto;
  padding-top: 1.25rem;
  /* display: flex; */
  /* justify-content: space-between; */
  /* align-items: center; */
}
.form {
  width: 95%;
  margin: auto;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.input {
  background-color: transparent;
  outline: none;
  border: none;
  border-bottom: 0.0625rem solid white;
  width: 80%;
  font-size: 25px;
  color: white;
  text-shadow: 1px 1px black;
}
.geo {
  display: flex;
  justify-content: center;
  width: 70%;
  margin: auto;
  color: white;
  text-align: center;
  font-size: 20px;
}
.geo img {
  width: 70px;
  height: 80px;
}
.star {
  transition: 400ms;
  vertical-align: middle;
  margin-bottom: 2%;
}
.star:hover {
  transform: scale(1.2);
}
.error {
  color:red;
}

@media (min-width: 768px){
  .header {
    width: 65%;
  }
  .input {
    font-size: 35px;
  }
}
@media (min-width: 960px){
  .header {
    width: 90%;
    margin: auto;
    padding-top: 1.25rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row-reverse
  }
  .form {
    width: 50%;
    margin: auto;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  .input {
    background-color: transparent;
    outline: none;
    border: none;
    border-bottom: 0.0625rem solid white;
    width: 90%;
    font-size: 25px;
    color: white;
    text-shadow: 1px 1px black;
  }
  .geo {
    display: flex;
    justify-content: center;
    width: 21%;
    margin: auto;
    color: white;
    text-align: center;
    font-size: 20px;
  }
}
@media(min-width: 1200px){
  .header {
    width: 90%;
    padding-top: 1.25rem;
    display: flex;
    justify-content: space-between;
    flex-direction: row-reverse
  }
  .form {
    width: 30%;
    margin: 0;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  .input {
    background-color: transparent;
    outline: none;
    border: none;
    border-bottom: 0.0625rem solid white;
    width: 90%;
    font-size: 25px;
    color: white;
    text-shadow: 1px 1px black;
  }
  .geo {
    display: flex;
    justify-content: center;
    margin: 0;
    width: 21%;
    color: white;
    text-align: center;
    font-size: 20px;
  } 
}